@import url(https://fonts.googleapis.com/css?family=Oswald);
@import url(https://fonts.googleapis.com/css?family=Lato);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.displayHorizontally {
  display: flex;
  flex-direction: row;
}

.displayVertically {
  display: flex;
  flex-direction: column;
}

.sAround {
  justify-content: space-around;
}

.sBetween {
  justify-content: space-between;
}

.sEvenly {
  justify-content: space-evenly;
}

.test {
  border: 2px solid black;
}

.width100 {
  width: 100%;
}

.width90 {
  width: 90%;
}

.width80 {
  width: 80%;
}

.centerSelf {
  align-self: center;
}


.portfolio-largeHello {
    font-size: 200px;
    margin: 0px;
    padding: 0px;
    font-family: 'Oswald';
  }
  
.portfolio-subheader {
    font-family: 'Lato', sans-serif;
    margin-bottom: 30px;
}

.portfolio-grid {
  align-self: center;
  margin: auto;
  display: grid;
  grid-template-columns: auto auto auto;
  grid-gap: 0px;
  padding: 10px;
}

.portfolio-grid > div {
  text-align: center;
  padding: 15px 0px 15px 0px;
}

@media only screen and (max-width: 850px) {
  .portfolio-grid {
    grid-template-columns: auto;
  }
  .portfolio-largeHello {
    font-size: 150px;
  }
}


*, *:before, *:after{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  
  body{
    background: #f9f9f9;
    font-size: 16px;
    font-family: 'Oswald', sans-serif;
  }
  
  .main-title{
    color: #2d2d2d;
    text-align: center;
    text-transform: capitalize;
    padding: 0.7em 0;
  }
  
  .container{
    padding: 1em 0;
    float: left;
    width: 50%;
  }
  @media screen and (max-width: 640px){
    .container{
      display: block;
      width: 100%;
    }
  }
  
  @media screen and (min-width: 900px){
    .container{
      width: 33.33333%;
    }
  }
  
  .container .title{
    color: #1a1a1a;
    text-align: center;
    margin-bottom: 10px;
  }
  
  .content {
    position: relative;
    width: 90%;
    max-width: 500px;
    margin: auto;
    overflow: hidden;
  }
  
  .content .content-overlay {
    background: white;
    position: absolute;
    height: 99%;
    width: 100%;
    left: 0;
    top: 0;
    bottom: 0;
    right: 0;
    opacity: 0;
    transition: all 0.15s ease-in-out 0s;
  }
  
  .content:hover .content-overlay{
    opacity: 1;
  }
  
  .content-image{
    width: 100%;
  }
  
  .content-details {
    position: absolute;
    text-align: center;
    padding-left: 0;
    padding-right: 0;
    width: 100%;
    top: 50%;
    left: 50%;
    opacity: 0;
    transform: translate(-50%, -50%);
    transition: all 0.3s ease-in-out 0s;
  }
  
  .content:hover .content-details{
    top: 50%;
    left: 50%;
    opacity: 1;
  }
  
  .content-details h3{
    color: black;
    font-weight: 500;
    letter-spacing: 0.15em;
    margin-bottom: 0.5em;
    text-transform: lowercase;
  }

  .content-details h5 {
      font-weight: 500;
      text-transform: lowercase;
  }
  
  .content-details p{
    color: black;
    font-size: 0.8em;
  }
  
  .fadeIn-bottom{
    top: 80%;
  }


  .popUpStyleParent {
    position: fixed;
    top: 50%;
    left: 50%;
    outline:none;
    transform: translate(-50%, -50%);
    align-items: center;
  }

  .linkOverride {
    color: black;
  }
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

#mainHeader {
    margin: auto;
    margin-top: 1.5em;
}

@media only screen and (max-width: 850px) {
    #mainHeader {
      width: 95%;
    }
}

.burger .burger-lines,
.burger .burger-lines:after,
.burger .burger-lines:before { 
